import React from "react";

function Quotes() {
  return (
    <div className="quote">
      <div className="naruto" title="Naruto">
        Quotify
      </div>
      <blockquote>
        Wake up to reality! nothing is planned in this world!
      </blockquote>
      <div title="Madara" className="character">
        Madara
      </div>
    </div>
  );
}

export default Quotes;
